body {
  font-family: "PT Sans", sans-serif;
}

.landing-container-header {
  font-size: 42px;
  text-align: center;
  transform: translateY(-50%);
  margin-top: 50vh;
}

.landing-container-content {
  /* background-color: #34495e; */
  background-color: white;
  height: 50vh;
  margin-top: 50vh;
  color: white;
}

.landing-container-content-languages h1 {
  font-size: 24px;
  width: max-content;
}
.landing-container-content-languages a {
  margin-left: 15px;
  color: white;
}

.landing-container-content-languages a:hover {
  text-decoration: underline;
}

.landing-container-content-languages {
  padding-top: 40vh;
  margin-left: 40vw;
}
.landing-container-content-language {
  display: flex;
  max-height: 50px;
  line-height: 50px;
  width: max-content;
}

.apod-container h3 {
  margin-right: 5vw;
  margin-left: 5vw;
  margin: 2.5vw;
}
.apod-container img,
.apod-container iframe {
  margin: 2.5vw;
}

.apod-container-2 {
  width: 80vw;
  margin: 0 10vw;
}
.apod-container-2 details {
  border: 1px solid #aaa;
  border-radius: 4px;
  padding: 0.5em 0.5em 0;
  margin-bottom: 20vh;
}

.apod-container-2 summary {
  margin: -0.5em -0.5em 0;
  padding: 0.5em;
  list-style-type: "+   ";
}

.apod-container-2 details[open] {
  padding: 0.5em;
}

.apod-container-2 details[open] summary {
  border-bottom: 1px solid #aaa;
  margin-bottom: 0.5em;
}
/*TODO: Fix color  */
.apod-container-2 details[open] p > u {
  color: blue;
}
@media only screen and (min-width: 800px) {
  .apod-container {
    display: flex;
  }

  .apod-container img,
  .apod-container iframe {
    max-height: 50vh;
  }
}

@media only screen and (max-width: 800px) {
  .apod-container img,
  .apod-container iframe {
    max-width: 90vw;
    margin: 5vw;
  }
}

.footer-left {
  float: left;
  margin-left: 20vw;
}

.footer-right {
  float: right;
  margin-right: 20vw;
}

.mars-rovers-container > div {
  display: flex;
}

.mars-rovers-container span {
  margin-right: 2vw;
}

.paginationButtons {
  width: 80%;
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
}

.paginationButtons a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid #2b2eff;
  color: #2b2eff;
  cursor: pointer;
}

.paginationActive a:hover {
  color: white;
  background-color: #2b2eff;
}

.paginationActive a {
  color: white;
  background-color: #2b2eff;
}

.paginationDisabled a {
  color: grey;
  background-color: grey;
}

.navbar-dropdown p {
  color: black;
  font-weight: 1000;
  padding-left: 1vw;
  text-decoration: underline;
}

.space-search-images {
  margin-left: 10vw;
  width: 80vw;
}

.navbar-dropdown .navbar-item {
  border-left: 2px solid hsl(217, 71%, 53%);
  padding: calc(0.75rem / 2);
  margin: calc(0.75rem / 2);
  margin-bottom: 0;
  margin-top: 0;
}
.navbar-dropdown {
  padding-top: 0;
}

.Main-Space {
  margin-top: 40vh;
  display: flex;
  justify-content: space-around;
  flex-direction: row;
}
@media only screen and (max-width: 800px) {
  .Main-Space {
    justify-content: space-around;
    flex-direction: column;
  }
}
.codeblock pre {
  font-family: "Fira Code", monospace;
  background-color: #34495e;
  color: white;
}
.npm-page {
  width: 50vw;
  margin-left: 25vw;
}

.npm-page-config {
  display: flex;
  justify-content: space-around;
  margin-top: 10vh;
  margin-bottom: 10vh;
}

.npm-page h1 {
  font-size: 24px;
}
.npm-page h1:hover::after {
  content: " #";
  cursor: pointer;
}

.npm-page > div {
  margin-bottom: 5vh;
}

code {
  font-family: monospace;
}

@import url("https://cdn.jsdelivr.net/gh/tonsky/FiraCode@1.206/distr/fira_code.css");

@import url("https://fonts.googleapis.com/css2?family=PT+Sans:wght@700&family=Roboto:wght@300&display=swap");
